* {
	font-family: 'Roboto', sans-serif; }

header {
	position: relative;
	background-color: #212121; }

.menu {
	display: flex;
	justify-content: space-between;
	padding: 10px 70px;
	margin: 0;
	list-style-type: none;
	.menu_link {
		color: #ffffff;
		font-weight: 300;
		font-size: 15px; }

	.menu_item {
		border-right: 2px dotted black;
		padding-right: 45px;
		padding-left: 0; } }


.subheader {
	position: absolute;
	width: 100%;
	background-color: rgba(0,0,0,.6);
	.subheader_logo {
		display: block;
		max-width: 100%;
		margin-top: 16px; }

	.subheader_official {
		margin-top: 6px;
		font-family: 'Roboto';
		opacity: 0.5;
		font-weight: 400;
		font-size: 13px;
		color: #FFFFFF;
		margin-bottom: 10px; }

	.subheader_call {
		font-family: 'Roboto';
		font-weight: 300;
		margin-top: 19px;
		color: #FFFFFF;
		font-size: 12px;
		text-align: right; }

	.subheader_phone {
		display: block;
		font-weight: 400;
		font-size: 22px;
		text-transform: uppercase;
		color: #FFFFFF;
		line-height: 12px;
		text-align: right;
		line-height: 12px;
		margin-top: 10px; }

	.subheader_btn {
		margin-top: 20px;
		width: 172px;
		height: 46px;
		background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
		box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
		border-radius: 4px;
		border: none;
		font-weight: 400;
		font-size: 14px;
		margin-right: 20px;
		text-transform: uppercase;
		color: #FFFFFF;
		text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21); } }


.promo {
	min-height: 800px;
	padding: 150px 0 135px 0;
	background: url(../css/img/uber_bg.png) center center/cover no-repeat;
	margin-bottom: 70px;

	.promo_header {
		font-weight: 900;
		font-size: 60px;
		text-transform: uppercase;
		line-height: 60px;
		margin: 0;
		color: #FFFFFF;
		margin-top: 20px;
		text-align: center; }

	.promo_subheader {
		text-transform: uppercase;
		font-size: 36px;
		font-weight: 700;
		color: #FFFFFF;
		line-height: 36px;
		margin: 0;
		margin-top: 22px;
		text-align: center; }

	.promo_descr {
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;
		text-align: center;
		margin-top: 23px; }

	.promo_btn {
		display: block;
		margin: 118px auto 0 auto;
		font-weight: 400;
		font-size: 18px;
		line-height: 63px;
		text-align: center;
		text-transform: uppercase;
		color: #FFFFFF;
		text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
		width: 259px;
		height: 63px;
		background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
		box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
		border-radius: 4px;
		border: none; } }


.label {
	margin: 0 auto;
	height: 24px;
	width: 112px;
	padding: 5px 12px;
	background-color: #000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 400;
	text-transform: uppercase;
	margin-bottom: 30px; }

.laber {
	margin-top: 60px;
	margin: 0 auto;
	height: 24px;
	width: 112px;
	padding: 5px 12px;
	background-color: #ffffff;
	color: #070716;

	font-size: 11px;
	font-weight: 400;
	text-transform: uppercase; }


.title {
	text-align: center;
	margin-top: 35px;
	margin-bottom: 20px;
	font-family: 'Roboto';
	font-weight: 700;
	font-size: 38px;
	line-height: 48px;
	text-transform: uppercase;
	color: #222222; }

.subtitle {
	font-family: 'Roboto';
	text-align: center;
	margin-top: 20px;
	font-weight: 400;
	font-size: 17px;
	color: #222222; }


.item {
	display: flex;
	padding-bottom: 25px; }

.advantages .advantages_card {
	padding-top: 25px;
	padding-left: 14px;
	padding-bottom: 22px;
	padding-right: 19px; }

.advantages .advantages_money {
	padding-top: 33px;
	padding-left: 19px;
	padding-right: 14px;
	padding-bottom: 30px; }

.advantages .advantages_picture {
	padding-top: 24px;
	padding-left: 24px;
	padding-right: 25px;
	padding-bottom: 20px;
	display: flex;
	justify-content: right; }

.advantages .advantages_calendar {
	padding-top: 19px;
	padding-right: 18px;
	padding-bottom: 21px;
	padding-left: 16px; }

.advantages .advantages_key {
	padding-top: 18px;
	padding-right: 28px;
	padding-bottom: 17px;
	padding-left: 31px; }

.advantages .advantages_map {
	padding-top: 23px;
	padding-right: 26px;
	padding-bottom: 23px;
	padding-left: 24px; }

.advantages .advantages_phone {
	padding-top: 20px;
	padding-right: 14px;
	padding-bottom: 27px;
	padding-left: 14px; }

.advantages .advantages_car {
	padding-top: 24px;
	padding-right: 24px;
	padding-bottom: 24px;
	padding-left: 19px; }

.advantages .advantages_circle {
	width: 113px;
	height: 113px;
	background: #1EACC7;
	border-radius: 100px; }

.advantages_descr {
	display: block;
	padding-left: 25px;
	height: 150px;
	font-family: 'Roboto';
	font-weight: 300;
	line-height: 21px;
	font-size: 15px;
	padding-right: 70px; }

.advantages .advantages_descr .advantages_heading {
	font-family: 'Roboto';
	font-weight: 400;
	font-size: 17px;
	line-height: 21px;
	color: #1EACC7; }

.mobile {
	background: url(../css/img/mobile_bg.png) center center/cover no-repeat;
	min-height: 864px;
	margin-top: 20px;
	padding-top: 47px;
	.row {}

	&_item {
		position: relative;
		min-height: 280px;
		max-width: 320px;
		margin-top: 20px;
		padding-top: 92px;
		&_1 {
			background: url(../css/img/mobile_screen_1.png) no-repeat;
			margin-top: 30px; }
		&_2 {
			background: url(../css/img/mobile_screen_2.png) no-repeat;
			margin-top: 30px; }
		&_3 {
			background: url(../css/img/mobile_screen_3.png) no-repeat;
			margin-top: 30px; }
		&_4 {
			background: url(../css/img/mobile_screen_4.png) no-repeat; }

		&_5 {
			background: url(../css/img/mobile_screen_5.png) no-repeat; }

		&_6 {
			background: url(../css/img/mobile_screen_6.png) no-repeat; }

		&_subtittle {
			text-align: center;
			font-family: 'Roboto';
			font-size: 24px;
			font-weight: 400;
			line-height: 31px;
			text-transform: uppercase;
			color: #ffffff; }
		&_plus {
			position: absolute;
			width: 26px;
			height: 26px;
			background-color: #000000;
			border-radius: 100%;
			margin: 0 auto;

			display: flex;
			justify-content: center;
			align-items: center;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			cursor: pointer; } } }

.choice {
	min-height: 500px;
	background: #070716;
	margin-top: 60px; }

